import React from "react";
import {inject, observer} from "mobx-react";

@inject('userStore')
@observer
export default class UserCreationForm extends React.Component {

  getUserDetails() {
    return {
      username: document.getElementsByName("username").value,
      password: document.getElementsByName("password").value
    }
  };

  add() {
    this.props.userStore.create(this.getUserDetails());
  }

  render() {
    return (
      <div>
        <input type="text" name="username"/>
        <br/>
        <input type="password" name="password"/>
        <br/>
        <input type="email" name="email"/>
        <button onClick={() => this.add()}>Войти
        </button>
      </div>
    );
  }
}


